import {data} from './data';
import {addMarkerOnMap, visiteDreamOnMap} from './map'


const dreamsContainer = document.body.querySelector('#dreams-container');


function buildAllDreams(){
    while (dreamsContainer.hasChildNodes()) {       //tant que #dreams-container a des enfants (hasChildNodes) tu supprimes les Ã©lements qui sont dans le #dreams-container
           
            dreamsContainer.removeChild(dreamsContainer.lastChild);    
    }
            data.forEach(buildOneDream);
            addDreamsListeners();
}

function buildOneDream(dream) {

    const dreamElement = document.createElement("div");
    dreamElement.innerHTML = `            
    
    <div class="card text-center" id="${dream.id}">    

    <h4 class="card-header font-weight-bold">${dream.description}</h4>
    <img src="${dream.imagePath}" class="card-img-top" alt="${dream.description}">  

    <div class="card-body">
    <a href="#" class="button-action btn btn-${dream.done?"secondary":"danger"} font-weight-bold btn-block">${dream.done?"Je veux le refaire":"Je me lance !"}</a></div>
  
    <div class="card-footer text-muted text-right">
        <a href="#" class="button-visit btn btn-outline-secondary btn-sm">Visiter</a>             
        <a href="${dream.link}" target="_blank" class="btn btn-outline-dark btn-sm">Plus d'infos</a>
    </div>
  
    </div>
`;

    dreamsContainer.appendChild(dreamElement);

    addMarkerOnMap(dream)

}

function addDreamsListeners() {
    
    document.querySelectorAll(".button-visit").forEach(item => {
        item.addEventListener("click", event => {
            visiteDream(item.parentElement.parentElement.getAttribute("id"));
        })
    });

    document.querySelectorAll(".button-action").forEach(item => {
        item.addEventListener("click", event => {
            toggleDreamDone(item.parentElement.parentElement.getAttribute("id"));
            buildAllDreams();
        })
    });
}

function toggleDreamDone(dreamId) {
    let dream = data.filter( item => item.id == dreamId)[0];
    dream.done = !dream.done;
}

function visiteDream(dreamId) {
    let position = data.filter( item => item.id == dreamId)[0].coordinates;
    visiteDreamOnMap(position);
}

export {buildAllDreams};